 /**
 * Mouse Position
 */
const cursor = {
    x: 0,
    y: 0
}

/**
 * Click Event Computer / Mobile
 */
if (!isMobile) {
    
    window.addEventListener('mousemove', (event) => {
        cursor.x = event.clientX / sizes.width * 2 - 1;
        cursor.y = - (event.clientY / sizes.height * 2 - 1);
    });

    // play/pause video with mouse-click on DOM
    document.body.addEventListener('mousedown', () => {

        checkMeshClick();
    });

} else {
    /* Mobile:
        Force-check cursor position based on touch event, then
        manually update raycaster and check intersecting objects of ray,
        at that exact moment of touch event.
    */
   
    window.addEventListener('touchstart', (touchEvent) => {

        cursor.x = touchEvent.targetTouches[0].clientX / sizes.width * 2 - 1;
        cursor.y = - (touchEvent.targetTouches[0].clientY / sizes.height * 2 - 1);

        raycaster.setFromCamera(cursor, camera);
        intersects = raycaster.intersectObjects(objectsToIntersect);
        
        if (intersects.length > 0) currentIntersect = intersects[0].object;
        else currentIntersect = null;
        checkMeshClick();
    });
}

/**
 * Load Manager and Loading Bar
 */
const loadingText = document.querySelector('.loadingText');
const loadingBar = document.querySelector('.loadingBar');

const loadingManager = new THREE.LoadingManager(

    // load complete
    () => {
        console.log('all assets loaded.');
        gsap.delayedCall(0.2, () => {
            gsap.to(overlayMat.uniforms.uAlpha, { duration: 2.0, value: 0.5 });
            loadingBar.style.transform = '';
            loadingBar.classList.add('ended');
            loadingText.innerHTML = '';
            startBtn.style.display = 'block';
        });
    },
    (itemUrl, itemsLoaded, itemsTotal) => {
        console.log('loading in progress...');
        const progressRatio = itemsLoaded / itemsTotal;
        loadingBar.style.transform = `scaleX(${progressRatio})`;
    }

);

/**
 * Loading Overlay
 */
const overlayGeo = new THREE.PlaneGeometry(20, 20, 1, 1);
const overlayMat = new THREE.ShaderMaterial({
    transparent: true,
    uniforms: {
        uAlpha: { value: 1.0 }
    },
    vertexShader:`
        void main() {
            gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
        }
    `,
    fragmentShader:`
        uniform float uAlpha;

        void main() {
            gl_FragColor = vec4(0.0, 0.0, 0.0, uAlpha);
        }
    `,
});
const overlay = new THREE.Mesh(overlayGeo, overlayMat);
overlay.rotation.y = -Math.PI / 2;
overlay.position.set(-5, 5, -1);

/**
 * Start Btn
 */
const startBtn = document.querySelector('.startBtn');
let started = false;

startBtn.addEventListener('click', () => {
    gsap.to(overlayMat.uniforms.uAlpha, { duration: 1.0, value: 0.0 });
    setTimeout( () => {
        scene.remove(overlay);
        controls.enabled = true;
    }, 1000);
    started = true;
    isPlaying = true;
    video.volume = 0.02;
    video.play();
    startBtn.style.display = 'none';
    scene.add(particles);
});

/**
 * HowTo Button
 */
 const queryBtn = document.querySelector('.howTo');
 const modalContainer = document.querySelector('.modalContainer');

queryBtn.addEventListener('click', () => {
    modalContainer.classList.toggle('visible');
});
modalContainer.addEventListener('click', () => {
    modalContainer.classList.toggle('visible');
});

/**
 * Footer expand function
 */
const footer = document.querySelector('footer');

footer.addEventListener('click', () => {
    footer.classList.toggle('openFooter');
});

/**
 * Raycaster for TV
 */
 const raycaster = new THREE.Raycaster();
 let tvScreenMesh;
 let objectsToIntersect = [];
 let intersects = [];
 let currentIntersect;

 // Points of Interest - html+webgl
 const points = [
    {
        position: new THREE.Vector3(3.5, 2.0, -3.5),
        element: document.querySelector('.point-0')
    },
    {
        position: new THREE.Vector3(2.9, 1.5, 3.5),
        element: document.querySelector('.point-1')
    },
    {
        position: new THREE.Vector3(0.5, 1.7, -2.0),
        element: document.querySelector('.point-2')
    },
    {
        position: new THREE.Vector3(4.25, 4.0, 3.0),
        element: document.querySelector('.point-3')
    },
    {
        position: new THREE.Vector3(-3.3, 5.5, -4.25),
        element: document.querySelector('.point-4')
    },
    {
        position: new THREE.Vector3(4.15, 3.5, -3.5),
        element: document.querySelector('.point-5')
    },
    {
        position: new THREE.Vector3(-2.5, 1.38, 0),
        element: document.querySelector('.point-6')
    },
    {
        position: new THREE.Vector3(6.5, 5.0, -5.5),
        element: document.querySelector('.point-7')
    },
    {
        position: new THREE.Vector3(-2.3, 2.1, -4.35),
        element: document.querySelector('.point-8')
    },
     {
        position: new THREE.Vector3(5.0, 3.5, -0.5),
        element: document.querySelector('.point-9')
     },
     {
        position: new THREE.Vector3(0, 30.0, 0),
        element: document.querySelector('.point-10')
     },
     {
        position: new THREE.Vector3(-0.87, 2, 0),
        element: document.querySelector('.point-11')
     },
     {
        position: new THREE.Vector3(0, -0.1, 0),
        element: document.querySelector('.point-12')
     },
 ];

/**
 * Base
 */

// Canvas
const canvas = document.querySelector('canvas.webgl');

// Scene
const scene = new THREE.Scene();
// add overlay:
scene.add(overlay);

/**
 * Loaders 
 */

// TextureLoader
const textureLoader = new THREE.TextureLoader(loadingManager);

// Mesh UV Texture
const bakedTexture = textureLoader.load('baked2tvON.jpg');
bakedTexture.flipY = false;
bakedTexture.encoding = THREE.sRGBEncoding;

// Anya
const anyaTexture = textureLoader.load('./posters/anyaPoster.png');
anyaTexture.flipY = false;
anyaTexture.encoding = THREE.sRGBEncoding;
// Zhong Li
const zhongLiTexture = textureLoader.load('./posters/bigdongzhong.png');
zhongLiTexture.flipY = false;
zhongLiTexture.encoding = THREE.sRGBEncoding;

// Star Particle Texture
const particleTexture = textureLoader.load('./particles/star.png');

/* Video for TV */
let isPlaying = false;
const video = document.getElementById('video');
const spyXFamilyEndingTexture = new THREE.VideoTexture( video );
spyXFamilyEndingTexture.flipY = false;
spyXFamilyEndingTexture.encoding = THREE.sRGBEncoding;

// DracoLoader
const dracoLoader = new DRACOLoader();
dracoLoader.setDecoderPath('draco/');

// GLTFLoader
const gltfLoader = new GLTFLoader(loadingManager);
gltfLoader.setDRACOLoader(dracoLoader);

/**
 * Materials
 */

// Baked Material
const bakedMaterial = new THREE.MeshBasicMaterial({
    map: bakedTexture,
});

// Anya Poster Material
const anyaMaterial = new THREE.MeshBasicMaterial({
    color: '#a19f9f',
    map: anyaTexture,
});

// Zhong Li Poster Material
const zhongLiMaterial = new THREE.MeshBasicMaterial({
    color: '#a19f9f',
    map: zhongLiTexture,
});

// TV Screen Material
const tvScreenMaterial = new THREE.MeshBasicMaterial({
    map: spyXFamilyEndingTexture,
})

/**
 * Model Load
 */
gltfLoader.load(
    'room2.glb',
    (gltf) => {
        gltf.scene.traverse( (child) => {
            child.material = bakedMaterial;
        });
        
        // get objects to be changed
        const anyaPosterMesh = gltf.scene.children.find( (child) => child.name === 'anya');
        const zhongLiPosterMesh = gltf.scene.children.find( (child) => child.name === 'zhongli');
        tvScreenMesh = gltf.scene.children.find( (child) => child.name === 'tvScreen');
        objectsToIntersect.push(tvScreenMesh);

        // change material
        anyaPosterMesh.material = anyaMaterial;
        zhongLiPosterMesh.material = zhongLiMaterial;
        tvScreenMesh.material = tvScreenMaterial;

        //
        scene.add(gltf.scene);
    },
    () => {},
    (error) => {
        console.log(error);
    }
);

/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () => {

    // Update sizes
    sizes.width = window.innerWidth;
    sizes.height = window.innerHeight;

    // Update camera
    camera.aspect = sizes.width / sizes.height;
    camera.updateProjectionMatrix();

    // Update renderer
    renderer.setSize(sizes.width, sizes.height);
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
});

/**
 * Camera
 */
const camera = new THREE.PerspectiveCamera(45, sizes.width/sizes.height, 1, 100);
camera.position.set(-25, 20, 3);
scene.add(camera);

/**
 * Test cube
 */
// const cube = new THREE.Mesh(
//     new THREE.BoxGeometry(0.1, 0.1, 0.1),
//     new THREE.MeshBasicMaterial({color: 'white'})
// );
// cube.position.set(5.0, 3.5, -0.5);
// scene.add(cube);

/**
 * Star Particles
 */

// manual position of particle points in 3D space
const vertices = [
    4.15, 0, -0.2,
    3.8, 0, 0.2,
    3.9, 0, -3.8,
    3.6, 0, -3.2
];
const particleGeo = new THREE.BufferGeometry();
particleGeo.setAttribute('position', new THREE.Float32BufferAttribute(vertices, 3));

const particleMat = new THREE.PointsMaterial({
    map: particleTexture,
    transparent: true,
    alphaMap: particleTexture,
    depthWrite: false,
    blending: THREE.AdditiveBlending,
    color: '#ffd70f',
    size: 1.0,
    sizeAttenuation: true,
});
const particles = new THREE.Points(particleGeo, particleMat);

/**
 * Controls
 */
const controls = new OrbitControls(camera, canvas);
controls.enableDamping = true;
controls.target.copy( new THREE.Vector3(0, 3, 0) );
controls.enabled = false;
controls.maxDistance = 50;

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    antialias: true
});
renderer.setSize(sizes.width, sizes.height);
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
renderer.outputEncoding = THREE.sRGBEncoding;


/**
 * Animate
 */

const clock = new THREE.Clock();

const tick = () => {

    const elapsedTime = clock.getElapsedTime();

    // Raycasting
    if (started) {

        // Raycast TV screen interaction
        raycaster.setFromCamera(cursor, camera);
        intersects = raycaster.intersectObjects(objectsToIntersect);
    
        if (objectsToIntersect.length > 0) {
            if (intersects.length > 0) {
                document.body.style.cursor = 'pointer';
                currentIntersect = intersects[0].object;
            } else {
                document.body.style.cursor = 'default';
                currentIntersect = null;
            }
        }

        // Raycast for html element placement and interaction
        for (const point of points) {
            
            const screenPosition = point.position.clone();
            screenPosition.project(camera);
        
            raycaster.setFromCamera(screenPosition, camera);
            intersects = raycaster.intersectObjects(scene.children, true);
        
            if (intersects.length === 0) {
                point.element.classList.add('visible');
            } else {
                const intersectionDistance = intersects[0].distance;
                const pointDistance = point.position.distanceTo(camera.position);
        
                if (intersectionDistance < pointDistance) {
        
                    point.element.classList.remove('visible');
                } else {
                    
                    point.element.classList.add('visible');
                }
            }
        
            // Translate points to correct position
            const translateX = screenPosition.x * sizes.width * 0.5;
            const translateY = -screenPosition.y * sizes.height * 0.5;
            point.element.style.transform = `translate(${translateX}px, ${translateY}px)`;
        }
    }

    // Animate Particles
    for (let i = 0; i < vertices.length / 3; i++) {

        const i3 = i * 3;
        const z = particleGeo.attributes.position.array[i3];
        particleGeo.attributes.position.array[i3 + 1] = Math.sin(elapsedTime * z * 0.6) / 5 + 5;
    }
    particleGeo.attributes.position.needsUpdate = true;

    // Update controls
    controls.update();

    // Renderer
    renderer.render(scene, camera);

    // Call tick for next frame
    window.requestAnimationFrame(tick);
}

tick();


function checkMeshClick() {

    if (currentIntersect === objectsToIntersect[0]) {
        if (isPlaying === false) {
            video.play();
            video.volume = 0.01;
            isPlaying = true;
            scene.add(particles);
        } else {
            video.pause();
            isPlaying = false;
            scene.remove(particles);
        }
    }
}